import Features from "../components/Features/Features"
import { ArtworkList } from "../components/ArtworkList"

const Index = ({ pageContext: { artist: cfArtist } }) => {
    const artist = Artist.registerFromContentful(cfArtist)
    return (<>
            <Seo artist={artist} />
            <Layout>
                <BannerModule
                    title={"Welcome to " + artist.name}
                    subTitle={artist.about}
                />
                <Features artworks={artist.featuredArtwork}
                          title={"Featured Works from " + artist.name + "."}
                          introduction="Vivamus quam mauris, pulvinar vel mauris id, interdum semper neque. Proin malesuada libero eget tellus scelerisque, id egestas tortor egestas."
                />
                <ArtworkList artworks={artist.artwork} />
            </Layout>
        </>)
}

export default Index
